<template>
    <section>
      <div v-if="isLoading" class="app--day" v-bind:style="styleObject">
        <span class="header">
          <span class="header--value">
            <custom-icon name="cloud" base-class="custom-icon"></custom-icon>
            {{ cloud }}
          </span>
          <span class="header--value">
            <custom-icon name="droplet" base-class="custom-icon"></custom-icon>
            {{ humidity }}
          </span>
          <span class="header--value">
            <custom-icon name="wind" base-class="custom-icon"></custom-icon>
            {{ wind }}
          </span>
        </span>
        <span class="temperature">
          <span class="temperature_left">
            {{ curTemp }}
          </span>
          <span class="temperature_right">
            {{ tempScale }}
          </span>
        </span>
        <span class="city">
          {{ city }}
        </span>
        <span class="weatherMain">
          {{ weatherMain }}
        </span>
        <span class="weather">
          <custom-icon :name="weatherIcon" base-class="custom-icon"></custom-icon>
        </span>
      </div>
    </section>
</template>

<script>
  import customIcon from 'vue-icon/lib/vue-feather.esm'

  export default{
  name:'Weather',
  components :{
  customIcon
  },
  data(){
  return{
  dataObj: null,
  latitude: 0.0,
  longitude: 0.0,
  city: '',
  curTemp: null,
  tempScale: '',
  tempMax: null,
  tempMin: null,
  cloud: null,
  wind: null,
  humidity: null,
  weatherMain: '',
  weatherDesc: '',
  errorMsg: '',
  baseClass: 'v-icon',
  styleObject: {
  background: ''
  },
  isLoading: false
  }
  },
  computed: {
  weatherIcon: function() {
  if (this.dataObj != null){
  var weatherID = this.dataObj.weather[0].id;
  if (weatherID >= 200 && weatherID <= 232) {
  this.styleObject.background = 'linear-gradient(45deg, #00ECBC, #007ADF)';
  return 'cloud-lightning';
  } else if (weatherID >= 300 && weatherID <= 321) {
  this.styleObject.background = 'linear-gradient(45deg, #89F7FE, #66A6FF)';
  return 'cloud-drizzle';
  } else if (weatherID >= 500 && weatherID <= 531) {
  this.styleObject.background = 'linear-gradient(45deg, #00C6FB, #005BEA)';
  return 'cloud-rain';
  } else if (weatherID >= 600 && weatherID <= 622) {
  this.styleObject.background = 'linear-gradient(45deg, #7DE2FC, #B9B6E5)';
  return 'cloud-snow';
  } else if (weatherID >= 701 && weatherID <= 781) {
  this.styleObject.background = 'linear-gradient(45deg, #D7D2CC, #304352)';
  return 'cloud';
  } else if(weatherID == 800) {
  this.styleObject.background = 'linear-gradient(45deg, #FEF253, #FF7300)';
  return 'sun';
  } else if (weatherID >= 801 && weatherID <= 804) {
  this.styleObject.background = 'linear-gradient(45deg, #17ead9, #6078ea)';
  return 'cloud';
  } else{
  this.styleObject.background = 'linear-gradient(45deg, #17ead9, #6078ea)'
  return 'alert-circle'
  }
  }
  this.styleObject.background = 'linear-gradient(45deg, #17ead9, #6078ea)'
  return 'alert-circle';
  }
  },
  methods: {
  getLocation: function () {
  if (!navigator.geolocation) {
  this.errorMsg = "Geolocation is not supported by your browser";
  this.city = this.errorMsg;
  console.warn(this.errorMsg);
  return;
  }
  var options = { timeout: 60000 };
  navigator.geolocation.getCurrentPosition(this.success, this.error, options);
  },
  success: function (position) {
  this.latitude = position.coords.latitude;
  this.longitude = position.coords.longitude;
  this.latitude = parseFloat(this.latitude).toFixed(2);
  this.longitude = parseFloat(this.longitude).toFixed(2);

  this.getWeather();
  },
  error: function (err) {
  this.errorMsg = "Unable to retrieve your location";
  this.city = this.errorMsg;

  console.warn(`ERROR(${err.code}): ${err.message}`);
  console.warn(this.errorMsg);
  },
  getWeather: function () {
  var reqURL = 'https://cors-anywhere.herokuapp.com/http://api.openweathermap.org/data/2.5/weather?lat=' + this.latitude + '&lon=' + this.longitude + '&APPID=69d3cf86b46f19cf3e049339355533aa';

  this.$http.get(reqURL).then(function (response) {
  this.dataObj = response.data;
  this.curTemp = (this.dataObj.main.temp - 273.15).toFixed(0);
  this.tempMax = (this.dataObj.main.temp_max - 273.15).toFixed(0) + '';
  this.tempMin = (this.dataObj.main.temp_min - 273.15).toFixed(0) + '';
  this.cloud = (this.dataObj.clouds.all) + '%';
  this.wind = (this.dataObj.wind.speed) + 'm/s';
  this.humidity = (this.dataObj.main.humidity) + '%';
  this.city = this.dataObj.name + ', ' + this.dataObj.sys.country;
  this.weatherMain = this.dataObj.weather[0].main;
  this.tempScale = '';
  this.isLoading = true;
  }, function (response) {
  //console.log(response);
  });
  }
  },
  created(){
  this.getLocation();
  }
  }
</script>

<style>
  @import url('https://fonts.googleapis.com/css?family=Open+Sans');

  .v-icon, .custom-icon {
  width:25px;
  height:25px;
  color: rgba(255, 255, 255, 0.9);
  }

  section{
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  color: rgba(255, 255, 255, 0.9);
  text-transform: uppercase;
  font-weight: 700;
  }

  .app--day{
  background-position:50%;
  background-size:cover;
  align-items:center;
  flex-direction: column;
  justify-content: space-around;
  display:flex;
  background: linear-gradient(45deg, #17ead9, #6078ea);
  width: 330px;
  height: 600px;
  border-radius: 5px;
  box-shadow: 0 19px 38px rgba(0,0,0,.3), 0 15px 12px rgba(0,0,0,.22);
  }

  .header{
  width:75%;
  display: flex;
  flex-direction:row;
  justify-content:space-around;
  padding-top:30px;
  text-transform: lowercase;
  }

  .header--value{
  padding-top:12px;
  margin-left:0;
  }

  .header--value svg{
  vertical-align: top;
  }

  .temperature{
  display: flex;
  flex-direction:row;
  justify-content:center;
  padding-top:80px;
  }

  .temperature_left{
  width:100%;
  display: flex;
  flex-direction:column;
  justify-content:space-evenly;
  font-size: 7em;
  color: hsla(0,0%,100%,.75);
  }

  .temperature_right{
  font-size: 4em;
  font-weight: 700;
  color: hsla(0,0%,100%,.75);
  padding-bottom: 10em;
  }

  .weather svg{
  width: 12em;
  height: 12em;
  padding-bottom: 5em;
  }

  .city{
  padding-top:50px;
  font-weight: 700;
  }

  .weatherMain{
  padding-top:10px;
  text-transform:lowercase;
  }

  .weatherMain:first-letter{
  text-transform:uppercase;
  }

</style>



