<template>
  <div id="app">
      <Weather></Weather>
  </div>
</template>

<script>
import Weather from './components/Weather.vue'

export default {
    name: 'app',
    components :{
        'Weather' : Weather
    }
}
</script>

<style>
#app{
  display:flex;
  align-items:center;
  justify-content: space-around;
}
</style>



